@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.li-header {
    font-family: 'Raleway@weight:600', sans-serif;
}
.footer {
    background-color: #343A40;
    text-align: center;
    color: white;
}

.footer-link {
    color: white;
    text-decoration: underline;
    text-align: center;
}

.footer-link:hover {
    color: #bdbdbd
}
.landing-top {
    /* maybe replace with bootstrap classes */
    padding-top: 10%;
    
}

.landing-head {
    /* maybe replace with bootstrap classes */
    margin-top: 5%;
}

.name {
    font-size: 1.5em;
}

.tagline {
    font-family: 'Dancing Script', sans-serif;
    /* My chosen margin is so large that Bootstrap's default classes aren't large enough.
    As soon as I learn Sass, I can add more sizes to by adding entries to the Sass map variable */
    margin-top: 15%;
}

@media only screen and (max-width: 668px) {
    .landing-top {
        padding-top: 25%;
    }
    .landing-head {
        font-size: 2.5rem;
    }
    .subHead {
        font-size: 1.5rem;
    }
}
.hrAbout {
    width: 80%;
    background-color: gray;
    border: 10px;
    height: 3px;
}

.aboutContainer {
    max-width: 100%;
}

.aboutHead {
    font-size: 5rem;
    margin-bottom: 20px;
    margin-top: 50px;
}

.headshot {
    max-width: 315px;
    height: auto;
    border-radius: 50%;
    width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.listContainer {
    margin: 5px;
    height: 100%;
}

.opaque {
    background: rgba(255, 255, 255, 0.8);
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media only screen and (max-width: 668px) {
    .aboutHead {
        font-size: 3rem;
        padding-bottom: 20px;
    }
    .bio {
        font-size: 1rem;
    }
}
.project {
    color: green;
}

.card {
    background-color: darkgray;
    /* could be replaced by bootstrap class */
    margin-bottom: 20px;
}

.card-shadow {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card-img-top {
    border: gray solid 1px;
}

.portHr {
    height: 2px;
    background-color: #3d3d3d;
    border-radius: 10px;
    width: 85%;
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.card-opaque {
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 5px 5px 7px grey, -5px 5px 7px grey;
    padding: 13px;
    border-radius: 5px;
}

.portfolioButton {
    background-color: #303048;
    color: white;
}

.portfolioButton:hover {
    color: gray;
}
h1.portHead {
    font-family:'Raleway@weight:700', sans-serif;
    /* My chosen margin is so large that Bootstrap's default classes aren't large enough.
    As soon as I learn Sass, I can add more sizes to by adding entries to the Sass map variable */
    margin-top: 15%;
    margin-bottom: 40px;
    font-size: 5rem;
}

.contactIcon {
    text-shadow: 2px 2px black;
    font-size: 100px;
    color: white;
}

.contactIcon:hover {
    color: #cecece
}

.contactIcon:active {
    text-shadow: none;
}
body {
    background-image: url(/react-portfolio/static/media/smaller-berger.f17bf190.jpg);
    background-position: left top;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    /* Background color that will be displayed while the background image is loading */
    background-color: black;
    font-family: 'Raleway@weight:600', sans-serif;
}

html, body {
    max-width: 100%;
    overflow-x: hidden;
}

h1 {
    font-size: 4em;
    /* Text-shadows are disabled in Bootstrap by default, but after I learn Sass I can change that setting and use their classes. */
    text-shadow: 2px 2px black;
}

h2 {
    /* font-size: 2em; */
    text-shadow: 2px 2px black;
}

h4, p, a {
    font-family: 'Roboto', sans-serif;
}

h1.portHead {
    font-family:'Raleway@weight:700', sans-serif;
    /* My chosen margin is so large that Bootstrap's default classes aren't large enough.
    As soon as I learn Sass, I can add more sizes to by adding entries to the Sass map variable */
    margin-top: 15%;
    margin-bottom: 100px;
    font-size: 5rem;
}


@media only screen and (max-width: 668px) {
    h1 {
        font-size: 2rem;
    }
    h2 {
        margin: 50px;
        text-align: center;
    }
    #pushDown {
        margin-top: 100px;
    }
}
